<img src="https://1.bp.blogspot.com/-OzAWaO1xmHw/X9XipD4semI/AAAAAAAACt8/s-vPFqGk_BALgRMce0iyd3Wv43mn32z7wCLcBGAsYHQ/s16000/Inside%2BTour%2Bof%2BGoogle%2527s%2BMassive%2BHeadquarter.webp" className="img-fluid center" alt="" style={{ borderRadius: "10px", padding:"0"}} ></img>
